:root {
  --mainViewBgColor: rgb(46, 41, 58);
}

.main-view-content-wrapper {
  display: grid;
  grid-template-columns: 1fr 1.5fr;
}

.main-view-trending {
  height: 1000px;
}

.main-view-trending-info {
  background: -webkit-gradient(linear,left top, right top,from(rgba(46, 41, 58, 0.7)), to(var(--mainViewBgColor)));
  background: -webkit-linear-gradient(left,rgba(46, 41, 58, 0.7), var(--mainViewBgColor));
  background: -o-linear-gradient(left,rgba(46, 41, 58, 0.7), var(--mainViewBgColor));
  background: linear-gradient(to right,rgba(46, 41, 58, 0.7), var(--mainViewBgColor));
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  height: 100%;
  color: white;
  padding-left: 80px;
  position: relative;
}

.main-view-trending-info-content-wrapper {
  position: absolute;
  top: 550px;
}

.trending-label {
  margin: 0;
  letter-spacing: 3px;
  font-size: 0.9em;
}

.trending-title {
  font-size: 3em;
  font-weight: 700;
  margin: 20px 0;
}

.trending-genre-runtime {
  margin: 0;
  letter-spacing: 2px;
  font-size: 0.9em;
}

.trending-buttons {
  margin-top: 55px;
  display: -ms-flexbox;
  display: flex;
}

.main-view-browsing-options {
  background: var(--mainViewBgColor);
  height: 1000px;
}

.main-view-trailer-button {
  margin-right: 15px;
}

@media (max-width: 414px) {
  .main-view-content-wrapper {
    grid-template-columns: 1fr;
  }
  .main-view-trending-info {
    background: -webkit-gradient(linear,left top, left bottom,from(rgba(46, 41, 58, 0.7)), to(var(--mainViewBgColor)));
    background: -webkit-linear-gradient(top,rgba(46, 41, 58, 0.7), var(--mainViewBgColor));
    background: -o-linear-gradient(top,rgba(46, 41, 58, 0.7), var(--mainViewBgColor));
    background: linear-gradient(to bottom,rgba(46, 41, 58, 0.7), var(--mainViewBgColor));
    padding: 0 20px;
  }
  .main-view-trending-info-content-wrapper {
    top: 200px;
  }
  .main-view-trending {
    height: 100vh;
  }
}


