.movie-view-wrapper {
  display: grid;
  grid-template-columns: 1fr 2fr;
  height: 1000px;
}

.movie-view-details-side {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  color: white;
  padding-left: 80px;
}

.movie-view-details-side-content-wrapper {
  position: relative;
  margin-right: -250px;
}

.movie-title {
  font-size: 3em;
  font-weight: 700;
  margin: 20px 0;
}

.movie-genre-runtime {
  margin: 0;
  letter-spacing: 2px;
  font-size: 0.9em;
  color: rgba(255, 255, 255, 0.7);
}

.movie-buttons {
  margin: 55px 0 80px 0;
  display: -ms-flexbox;
  display: flex;
}

.movie-overview-label {
  letter-spacing: 2px;
}

.movie-overview-text {
  color: rgba(255, 255, 255, 0.5);
  line-height: 25px;
}

.movie-view-img-side-gradient {
  background: -webkit-gradient(linear,left top, right top,from(var(--mainViewBgColor)), to(rgba(46, 41, 58, 0.7)));
  background: -webkit-linear-gradient(left,var(--mainViewBgColor), rgba(46, 41, 58, 0.7));
  background: -o-linear-gradient(left,var(--mainViewBgColor), rgba(46, 41, 58, 0.7));
  background: linear-gradient(to right,var(--mainViewBgColor), rgba(46, 41, 58, 0.7));
  height: 1000px;
}

.movie-view-img-side-content-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-line-pack: center;
      align-content: center;
  color: white;
}

.movie-cast-member-list-label {
  font-weight: 700;
  letter-spacing: 2px;
  margin: 70px 0 30px 0;
}

.loading {
  text-align: center;
  color: white;
  margin-top: 100px;
}

.movie-view-trailer-button {
  margin-right: 15px;
}

@media (max-width: 414px) {
  .movie-view-details-side {
    padding-left: 0; 
  }
  .movie-view-details-side-content-wrapper {
    position: static;
    margin-right: 0;
  }
  .movie-view-img-side {
    display: none;
  }
  .movie-view-wrapper {
    grid-template-columns: 1fr;
    margin-top: 100px;
    padding: 0 20px;
  }
  .movie-buttons {
    margin: 30px 0 50px 0;
  }
  .movie-cast-member-list-label {
    margin: 70px 0 30px 0;
    text-align: center;
  }
}


