.navigation-wrapper {
  position: absolute;
  width: 100%;
  top: 10px;
  left: 0;
  z-index: 1;
  padding: 0 20px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  font-family: 'Roboto Condensed', sans-serif;
}

.home-link {
  text-decoration: none;
}

.navigation-headline {
  color: white;
  font-size: 1.5em;
  margin-right: 50px;
}

.navigation-search-icon-area {
  --size: 50px;
  background: rgba(255, 255, 255, 0.25);
  height: var(--size);
  width: var(--size);
  padding: 10px;
  border-radius: var(--size);
}

.navigation-search-icon {
  color: white;
  height: 100%;
  width: 100%;
}

.navigation-search-icon:hover {
  cursor: pointer;
}



